.post {
	@include clearfix();
	margin: 0 auto;
	position: relative;
	z-index: 2;

	.csstransitions & {
		opacity: 0;
		transform: translate3d(-20px, 0, 0);
		transition: transform 1s 2s, opacity 2s 2s;

		@include mq($bp-tablet) {
			transform: translate3d(-50px, 0, 0);
		}
	}

	.go & {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}

	&__content {
		@include clearfix();
		@include font-size(16);
		margin: 20px;
		max-width: 1200px;
		position: relative;
		z-index: 2;

		@include mq($bp-tablet) {
			@include font-size(18);
			margin: 40px 50px 80px;
		}

		@include mq($bp-desktop, true) {
			@include font-size(20);
			margin: 40px 50px 120px;
		}
	}
}

// PHOTOSET GRID
.photoset-grid {
	margin-bottom: 20px;

	@include mq($bp-tablet, true) {
		margin-bottom: 40px;
	}
}

// VIDEO
.video {
	clear: both;
	display: block;
	float: left;
	margin: 0;
	width: 100%;

	&__wrapper {
		height: 0;
		margin: 0 auto;
		overflow: hidden;
		padding: 0 0 56.25%; // 16:9 ratio
		position: relative;
		width: 100%;

		// scss-lint:disable ImportantRule
		iframe,
		object,
		embed {
			height: 100% !important;
			left: 0;
			position: absolute;
			top: 0;
			width: 100% !important;
		}
		// scss-lint:enable ImportantRule
	}
}
