a {
	color: $color-accent;
	text-decoration: none;
	transition: color .2s ease-in-out;

	&:hover {
		color: $color-hover;
	}
}

h2 {
	@include font-size(18);
	color: $color-white;
	display: block;
	font-family: $font-title;
	line-height: 1;
	margin-bottom: 30px;
	position: relative;
	text-transform: uppercase;

	@include mq($bp-tablet, true) {
		@include font-size(28);
		margin-bottom: 40px;
	}

	a {
		color: $color-white;
		text-decoration: none;

		&:hover {
			color: $color-accent;
		}
	}

	span {
		display: none;
		position: absolute;
		right: 20px;
		top: 0;
	}
}
