.twitter {
	@include clearfix();
	margin: 20px 0 20px 20px;
	max-width: 1220px;
	position: relative;
	z-index: 2;

	@include mq($bp-tablet) {
		margin: 40px 30px 80px 50px;
	}

	@include mq($bp-desktop, true) {
		margin: 40px 30px 120px 50px;
	}

	.csstransitions & {
		opacity: 0;
		transform: translate3d(-20px, 0, 0);
		transition: transform 1s 2.8s, opacity 1s 2.8s;
	}

	.go & {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}

.tweets {
	@include clearfix();
	list-style: none;
}

.tweet {
	display: none;
	margin: 0;
	width: 100%;

	@include mq($bp-smalltablet) {
		float: left;
		width: 50%;
	}

	@include mq($bp-tablet) {
		width: 50%;

		&--3,
		&--4 {
			display: inline;
		}
	}

	@include mq($bp-desktop, true) {
		float: left;
		width: 33.33%;

		&--5,
		&--6 {
			display: inline;
		}
	}

	&--1,
	&--2 {
		display: inline;
	}

	a {
		// background: $color-black;
		@include font-size(16);
		background: rgba(0, 0, 0, .3);
		border: 2px solid $color-grey-darker;
		border-color: rgba(0, 0, 0, 0);
		border-radius: 3px;
		color: $color-grey;
		display: block;
		line-height: 1.4;
		margin: 0 20px 20px 0;
		padding: 50px 10px 15px;
		position: relative;
		text-align: left;
		transition: border .4s ease-in-out, background .4s ease-in-out, color .4s ease-in-out;

		@include mq($bp-tablet) {
			@include font-size(18);
		}

		@include mq($bp-desktop, true) {
			@include font-size(20);
		}

		span {
			border-bottom: 3px solid $color-accent;
			color: $color-accent;
			display: block;
			font-family: $font-title;
			font-size: 80%;
			font-weight: 900;
			left: 10px;
			padding-bottom: 5px;
			position: absolute;
			top: 10px;
		}

		&:hover,
		&:focus {
			//border-color: $color-grey-dark;
			//border: 5px solid #f8f8f8;
			//padding: 10px;
			background: rgba(0, 0, 0, .3);
			border-color: rgba(0, 0, 0, .3);
			color: $color-grey-light;
		}
	}

}
