.header {
	@include clearfix();
	display: inline-block;
	margin: 60px 20px 40px;
	padding-left: 60px;
	position: relative;
	text-decoration: none;
	transition: all 300ms;
	z-index: 3;

	@include mq($bp-tablet, true) {
		margin: 80px 50px 40px;
		padding-left: 70px;
	}

	@include mq($bp-desktop, true) {
		margin-bottom: 90px;
	}

	img {
		border-radius: 50%;
		display: inline;
		float: left;
		height: 48px;
		left: 0;
		margin-top: -24px;
		position: absolute;
		top: 50%;
		width: 48px;

		@include mq($bp-tablet, true) {
			height: 56px;
			margin-top: -28px;
			width: 56px;
		}

		.csstransitions & {
			// filter: blur(5px);
			opacity: 0;
			transform: translate3d(-100px, 0, 0) rotate(-360deg);
			transition: transform 700ms ease-in-out;
			transition-delay: 0;

			@include mq($bp-tablet) {
				transform: translate3d(-200px, 0, 0) rotate(-360deg);
			}
		}
	}

	h1 {
		@include font-size(28);
		color: $color-white;
		font-family: $font-title;
		line-height: 1;
		text-transform: uppercase;

		@include mq($bp-tablet, true) {
			@include font-size(36);
		}

		span {
			display: inline-block;

			.csstransitions & {
				filter: blur(5px);
				opacity: 0;
				transform: translate3d(-100px, -10px, 0);

				@include mq($bp-tablet) {
					transform: translate3d(-200px, -15px, 0);
				}
			}
		}
	}

	.csstransitions .go & {

		img {
			// filter: blur(0);
			opacity: 1;
			transform: translate3d(0, 0, 0) rotate(0);
			transition: transform 800ms ease-in-out;
		}

		h1 {
			span {
				// transform: translateZ(0);
				filter: blur(0);
				opacity: 1;
				transform: translate3d(0, 0, 0);
				// transition: all 550ms;

				&:nth-child(1) {
					transition: all 380ms 550ms;
				}

				&:nth-child(2) {
					transition: all 460ms 600ms;
				}

				&:nth-child(3) {
					transition: all 540ms 650ms;
				}

				&:nth-child(4) {
					transition: all 620ms 700ms;
				}

				&:nth-child(5) {
					transition: all 700ms 750ms;
				}

				&:nth-child(6) {
					transition: all 780ms 800ms;
				}

				&:nth-child(7) {
					transition: all 860ms 850ms;
				}

				&:nth-child(8) {
					transition: all 940ms 900ms;
				}
			}
		}
	}
}

.intro {
	margin: 20px 60px 50px 20px;
	max-width: 1200px;
	position: relative;
	z-index: 2;

	@include mq($bp-tablet) {
		margin: 40px 150px 90px 50px;
	}

	@include mq($bp-desktop, true) {
		margin: 40px 150px 120px 50px;
	}

	.csstransitions & {
		opacity: 0;
		transform: translate3d(-20px, 0, 0);
		transition: transform 1s 2s, opacity 2s 2s, margin 300ms;

		@include mq($bp-tablet) {
			transform: translate3d(-50px, 0, 0);
		}
	}

	.go & {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}

	p {
		@include font-size(28);
		line-height: 1.2;
		opacity: .8;

		@include mq(400) {
			font-size: 4vw;
		}

		// @include mq(960) {
		// 	font-size: 7vw;
		// }

		@include mq(1200, true) {
			@include font-size(48);
		}
	}
}

.bg {
	@include unselectable();
	left: 0;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;

	.csstransitions & {
		opacity: 0;
		transform: translate3d(0, -20px, 0) scale(1.1);
		transition: all 10000ms 1000ms;
	}

	.go & {
		opacity: 1;
		transform: translate3d(0, 0, 0) scale(1);
	}

	img {
		display: block;
		margin: 0;
		width: 100%;
	}
}
