$color-white : #ffffff;
$color-black : #000000;

// Tint : Slightly lighten a color
@function tint($color, $percentage) {
	@return mix($color-white, $color, $percentage);
}

// Shade : Slightly darken a color
@function shade($color, $percentage) {
	@return mix($color-black, $color, $percentage);
}

// Media-Queries
@mixin mq($point, $ie9: false, $query1: min, $query2: width) {
	@if $ie9 == true{
		.no-mediaqueries & {
			@content;
		}

		@media screen and (#{$query1}-#{$query2}: $point+px) {
			@content;
		}
	} @else {
		@media screen and (#{$query1}-#{$query2}: $point+px) {
			@content;
		}
	}
}

// Retina
@mixin retina() {
	@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min--moz-device-pixel-ratio: 1.5), only screen and (min-device-pixel-ratio: 1.5) {
		@content;
	}
}

// Clearfix
@mixin clearfix() {
	zoom: 1;

	&:before,
	&:after {
		content: '';
		display: table;
	}

	&:after {
		clear: both;
	}
}

@mixin unselectable() {
	-webkit-touch-callout: none;
	user-select: none;
}

// scss-lint:disable DuplicateProperty
@mixin font-size($font-family-size: 14) {
	font-size: #{$font-family-size}px;
	font-size: #{$font-family-size / 10}rem;
}
// scss-lint:enable DuplicateProperty

@mixin text-overflow() {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

@mixin fontawesome($icon) {
	font-family: 'FontAwesome';
	content: $icon;
	display: inline-block;
}
