.instagram {
	@include clearfix();
	margin: 20px 0 20px 20px;
	max-width: 1220px;
	position: relative;
	z-index: 2;

	@include mq($bp-tablet) {
		margin: 40px 30px 80px 50px;
	}

	@include mq($bp-desktop, true) {
		margin: 40px 30px 120px 50px;
	}

	.csstransitions & {
		opacity: 0;
		transform: translate3d(-20px, 0, 0);
		transition: transform 1s 2.5s, opacity 1s 2.5s;
	}

	.go & {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}

	&__item {
		display: none;
		float: left;
		margin: 0;
		width: 50%;

		.show & {

			@include mq($bp-mobile, false) {
				width: 33.333%;

				&--5,
				&--6 {
					display: inline;
				}
			}

			@include mq($bp-tablet, true) {
				width: 25%;

				&--5,
				&--6,
				&--7,
				&--8 {
					display: inline;
				}
			}

			&--1,
			&--2,
			&--3,
			&--4 {
				display: inline;
			}
		}
	}

	&__link {
		background: $color-grey-darker;
		display: block;
		margin: 0 20px 20px 0;
		overflow: hidden;
		position: relative;

		img,
		video {
			// filter: grayscale(1);
			opacity: 1;
			transform: translateZ(0);
			transition: opacity .4s ease-in-out, transform 1s .2s ease-in-out;
		}

		.no-touch &--video {
			img {
				display: none;
			}
		}

		.touch &--video {
			video {
				display: none;
			}
		}

		.touch &--video span,
		.no-video &--video span {
			height: 40px;
			left: 50%;
			margin-left: -20px;
			margin-top: -20px;
			position: absolute;
			top: 50%;
			width: 40px;
			z-index: 2;
		}

		&__image,
		&__video {
			display: block;
			position: relative;
			width: 100%;
			z-index: 1;
		}

		&__caption {
			display: none;
		}

		&:hover,
		&:focus {
			img,
			video {
				//filter: grayscale(0);
				opacity: .7;
				transform: scale(1.05);
			}
		}
	}
}
